@import "@/scss/settings.scss";





















































.page-projects{
    position: relative;
    max-width: 100vw;
    max-height: 100vh;
    height: 100vh;
    width: 100vw;
    max-width: 100vw;
    @media screen and (max-width : $medium){
        overflow: scroll !important;
    } 
    @media screen and (min-width : $medium){
        pointer-events: none;
    } 
}
.page__inner{
    display: flex;
    min-height: 100vh;
    pointer-events: none;
    max-width: 100vw;
    @media screen and (max-width : $medium){
        overflow: scroll !important;
    } 
}
